import React, {useState} from 'react';

type ContextType = {
    isSideNavOpen: boolean;
    setIsSideNavOpen: (isSideNavOpen: boolean) => void;
    textMovieFilter: string;
    setTextMovieFilter: (textMovieFilter: string) => void;
};

export const AppContext = React.createContext<ContextType>({
    isSideNavOpen: false,
    setIsSideNavOpen: () => {
        throw Error('setIsSideNavOpen not implemented');
    },
    textMovieFilter: '',
    setTextMovieFilter: () => {
        throw Error('setTextMovieFilter not implemented');
    },
});

export default function ContextProvider({
                                            children,
                                        }: {
    children: JSX.Element;
}) {
    const [isSideNavOpen, setIsSideNavOpen] = useState<boolean>(false);
    const [textMovieFilter, setTextMovieFilter] = useState<string>('');

    return (
        <>
            <AppContext.Provider
                value={{
                    isSideNavOpen,
                    setIsSideNavOpen,
                    textMovieFilter,
                    setTextMovieFilter
                }}>
                {children}
            </AppContext.Provider>
        </>
    );
}
